import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';
import Row from 'r-cmui/components/Row';
import Col from 'r-cmui/components/Col';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';
import Demo5 from './demo5';
import Demo6 from './demo6';
import Demo7 from './demo7';


class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>InputNumber </h1>
                <blockquote className='page-tip'>
                    
                </blockquote>

                <h1 className='page-h1'></h1>

                <Row>
                    <Col grid={0.5}>
                        <Label className='code-col'>
                            <Label className='code-box'>
                                <Demo1></Demo1>
                            </Label>
                        </Label>

                        <Label className='code-col'>
                            <Label className='code-box'>
                                <Demo3></Demo3>
                            </Label>
                        </Label>

                        <Label className='code-col'>
                            <Label className='code-box'>
                                <Demo5></Demo5>
                            </Label>
                        </Label>

                        <Label className='code-col'>
                            <Label className='code-box'>
                                <Demo7></Demo7>
                            </Label>
                        </Label>
                    </Col>
                    <Col grid={0.5}>
                        <Label className='code-col'>
                            <Label className='code-box'>
                                <Demo2></Demo2>
                            </Label>
                        </Label>
                        <Label className='code-col'>
                            <Label className='code-box'>
                                <Demo4></Demo4>
                            </Label>
                        </Label>
                        <Label className='code-col'>
                            <Label className='code-box'>
                                <Demo6></Demo6>
                            </Label>
                        </Label>
                    </Col>
                </Row>


                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>InputNumber</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'value', desc: '', type: 'string/number', default: '0'},
                    {param: 'min', desc: '', type: 'string/number', default: ''},
                    {param: 'max', desc: '', type: 'string/number', default: ''},
                    {param: 'step', desc: '', type: 'string/number', default: '1'},
                    {param: 'disabled', desc: '', type: 'boolean', default: 'false'},
                    {param: 'onChange', desc: '', type: 'function', default: ''},
                    {param: 'formatter', desc: '', type: 'function', default: ''},
                    {param: 'parser', desc: '', type: 'function', default: ''},
                    {param: 'itemClassName', desc: 'inputclass', type: 'string', default: ''},
                    {param: 'size', desc: 'largesmall', type: 'string', default: ''},
                    {param: 'name', desc: 'name', type: 'string', default: ''}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>disable()</i>
                        <span></span>
                    </li>
                    <li>
                        <i>enable()</i>
                        <span></span>
                    </li>
                    <li>
                        <i>setMin(min)</i>
                        <span></span>
                        <ul>
                            <li>
                                min {'Number'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setMax(max)</i>
                        <span></span>
                        <ul>
                            <li>
                                max {'Number'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setStep(step)</i>
                        <span></span>
                        <ul>
                            <li>
                                step {'Number'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setValue(value)</i>
                        <span></span>
                        <ul>
                            <li>
                                value {'Number'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getFormatedValue()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {'String'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getValue()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {'String'} 
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



